import React, { useState, useEffect } from "react";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  useToast,
  Text,
} from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
import { useParams } from "react-router";
import { Link, useNavigate } from "react-router-dom";
import { AiOutlineArrowLeft, AiOutlineQuestionCircle } from "react-icons/ai";
import { BiError, BiCheck } from "react-icons/bi";
import {
  setMandatesRecords,
  updateMandate,
  deleteMandate,
} from "../../actions/mandates";
import BorderedTable from "../../components/BorderedTable";
import { statusPill } from "../../common/ui/Pill";
import "../customer.css";
import { snakeToTitleCase } from "../../common/utils";
import MandatesMockData from "../../mockData/mandates";
import CardsMockData from "../../mockData/cards";

const MandatePage = () => {
  const [selectedCharge, setSelectedCharge] = useState(null);
  const [toPause, setToPause] = useState(true);
  const params = useParams();
  const { card_data: cardId, mandate_id } = params;
  const dispatch = useDispatch();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const toast = useToast();
  const navigate = useNavigate();

  const allMandateRecord = useSelector((state) => state.mandates);
  let Mandates = allMandateRecord[cardId] || [];
  Mandates = Mandates.filter((e) => e.id === mandate_id);

  const cardDetails = CardsMockData.filter(
    (e) => e.redacted_card_number === cardId
  )[0];

  useEffect(() => {
    if (Object.keys(allMandateRecord).length === 0) {
      dispatch(setMandatesRecords(MandatesMockData));
    }
  }, []);

  const handleMandate = () => {
    if (toPause) {
      // eslint-disable-next-line object-property-newline
      dispatch(
        updateMandate({
          cardId,
          id: mandate_id,
          status: Mandates[0].status === "active" ? "paused" : "active",
        })
      );
      toast({
        title: "Mandate Paused Successfully",
        status: "success",
        isClosable: true,
        position: "top-right",
        duration: 3000,
      });
    } else {
      // eslint-disable-next-line object-property-newline
      dispatch(deleteMandate({ cardId, id: mandate_id }));
      toast({
        title: "Mandate Deleted Successfully",
        status: "success",
        isClosable: true,
        position: "top-right",
        duration: 3000,
      });
      navigate(`/customer/mandates/${cardId}`);
    }

    onClose();
    setToPause(true);
  };

  const fields = [
    [
      "",
      (item, hideTick) => (
        <div className="table-data heimdall">
          {!hideTick && (
            <>
              {item.status === "FAILED" ? (
                <BiError className="red-error" />
              ) : (
                <BiCheck className="green-tick" />
              )}
            </>
          )}
          &nbsp;&nbsp;
          <div className="column">
            <span className="semi-bold">
              {snakeToTitleCase(Mandates[0].merchant)}
            </span>
            <span className="info-block">
              at {item.date} {item.time} <br />
              {item.reason && item.reason}
            </span>
          </div>
        </div>
      ),
    ],
    ["", (item) => <div className="table-data">{statusPill(item.status)}</div>],
    [
      "",
      (item) => (
        <div className="table-data semi-bold justify-center">{item.amount}</div>
      ),
    ],
  ];

  return (
    <div>
      <div className="top-bar grey-strip">
        <div className="customer-left row-start">
          <Link to={`/customer/mandates/${cardId}`}>
            <AiOutlineArrowLeft />
          </Link>{" "}
          <span className="bold">
            {Mandates[0].merchant} {statusPill(Mandates[0].status)}
          </span>
        </div>
        {Mandates[0].status !== "deleted" && (
          <div className="customer-left row-end">
            <button
              className="btn btn-hide-filters"
              onClick={() => {
                onOpen();
                setToPause(true);
              }}
            >
              {Mandates[0].status === "paused" ? "Resume" : "Pause"} Mandate
            </button>
            <button
              className="btn btn-hide-filters"
              onClick={() => {
                onOpen();
                setToPause(false);
              }}
            >
              Delete Mandate
            </button>
          </div>
        )}
      </div>

      <div className="box">
        <div className="wrapper-prashant heimdall">
          <div className="">
            <div className="detail-header">Upcoming Charge</div>
            <div className="table-bordered">
              <BorderedTable
                items={Mandates[0].upcoming_charge}
                fields={fields}
                onRowClick={setSelectedCharge}
                hideTick={true}
              />
            </div>
            <div className="detail-header">Previous Charges</div>
            <div className="table-bordered">
              <BorderedTable
                items={Mandates[0].previous_charges}
                fields={fields}
                onRowClick={setSelectedCharge}
              />
            </div>
          </div>

          <div className="">
            <div className="boogey">
              <div className="detail-header">Mandate Details</div>
              <div className="token-info">
                <div className="table-row">
                  <div className="table-data">Card</div>
                  <div className="table-data">
                    {cardDetails.network} {cardId}
                  </div>
                </div>
                <div className="table-row">
                  <div className="table-data">Max Amount</div>
                  <div className="table-data">{Mandates[0].max_amount}</div>
                </div>

                <div className="table-row">
                  <div className="table-data">Started on</div>
                  <div className="table-data">{Mandates[0].started_on}</div>
                </div>
                <div className="table-row">
                  <div className="table-data">End on</div>
                  <div className="table-data">{Mandates[0].end_on}</div>
                </div>
                <div className="table-row">
                  <div className="table-data">Frequency</div>
                  <div className="table-data">{Mandates[0].frequency}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal
        isOpen={isOpen}
        onClose={() => {
          onClose();
          setToPause(true);
        }}
        size="sm"
      >
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>
            {toPause ? "Pause" : "Delete"} this mandate?
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            {toPause && (
              <Text
                fontSize="12px"
                colorScheme="#344A6C"
                backgroundColor="rgba(205, 130, 20, 0.1)
              "
                padding="10px"
                mb="10px"
              >
                {Mandates[0].status === "active"
                  ? `
                Pausing your subscription could disrupt your products. Please check with your
                service provider, in case you’ve any questions.`
                  : `Please check with your service provider in case of any affect on your services`}
              </Text>
            )}
            {toPause
              ? Mandates[0].status === "active"
                ? "You will not be charged and your services will be on hold. Are you sure?"
                : "Your mandate will be resumed. You will be charged based on your original subscription cycle. Are you sure?"
              : "This action cannot be undone. Are you sure you want to delete this mandate?"}
          </ModalBody>

          <ModalFooter flexDirection="row" justifyContent="space-between">
            <button
              className="customer-btn btn-hide-filters equal-width"
              onClick={() => {
                onClose();
                setToPause(true);
              }}
            >
              No
            </button>
            <button
              className="customer-btn btn-hide-filters equal-width fill"
              onClick={handleMandate}
            >
              Yes
            </button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </div>
  );
};

export default MandatePage;
