import React from 'react';
import { snakeToTitleCase } from '../utils';

const statusPillClasses = {
  // Common
  created: 'label-semi-muted',
  failed: 'label-danger',
  pending: 'label-pending',
  cancelled: 'label-danger',
  processed: 'label-success',

  // payment
  authorized: 'label-info',
  captured: 'label-success',
  refunded: 'label-primary',

  initiated: 'label-info',

  // order
  attempted: 'label-info',
  paid: 'label-success',

  // billdesk
  null: 'label-pending',

  // batch
  processing: 'label-info',
  scheduled: 'label-pending',
  partially_processed: 'label-info',

  // refund
  partial: 'label-info', // payment.refund_status

  // invoice
  // draft: 'label-semi-muted',
  issued: 'label-info',

  // dispute
  open: 'label-danger',
  under_review: 'label-primary',
  represented: 'label-primary',
  won: 'label-success',
  lost: 'label-muted',

  // activation form status
  needs_clarification: 'label-pending',
  activated: 'label-success',
  // public features
  approved: 'label-success',
  rejected: 'label-danger',

  closed: 'label-danger',

  // credit types
  amount: 'label-primary',
  fee: 'label-info',
  refund: 'label-yellow',

  // activation flow
  whitelist: 'label-success',
  greylist: 'label-info',
  blacklist: 'label-danger',

  //POA, POI, and bank verification status
  verified: 'label-success',
  not_verified: 'label-danger',
  not_matched: 'label-danger',
  incorrect_details: 'label-info',

  // new settlement service //
  COMPLETE: 'label-success',
  SETTLEMENT_CREATED: 'label-success',
  SKIPPED: 'label-orange',
  FAILED: 'label-danger',
  REVERSED: 'label-yellow',
  PROCESSED: 'label-yellow',
  TERMINATED: 'label-danger',
  CREATED: 'label-primary',
  CREATION_IN_PROGRESS: 'label-pending',
  IN_MIGRATION: 'label-pending',
  IN_PROGRESS: 'label-pending',
  INITIATED: 'label-info',
  SCHEDULED: 'label-info',

  // Tokens //
  active: 'label-success',
  deleted: 'label-danger',
  expired: 'label-danger',
  suspended: 'label-muted',

  // Tokens Requestors //
  declined: 'label-danger',

  // Mandates
  paused: 'label-danger',
  SUCCESSFULL: 'label-success',
  UPCOMING: 'label-muted',

  ACTIVE: 'label-success',
  DELETED: 'label-danger',
};

export const statusPill = (status, emptyValue = '--') => {
  return status ? (
    <span className={`pill ${statusPillClasses[status] || 'label-semi-muted'}`}>
      {snakeToTitleCase(status)}
    </span>
  ) : (
    emptyValue
  );
};
