import React from 'react';
import { useSelector } from 'react-redux';
import { BrowserRouter as Router, Routes, Route, Navigate, Outlet } from 'react-router-dom';
import Layout from '../layout';
import OnBoarding from '../TR/OnBoarding';
import TRStatus from '../TR/Status';
import AllCards from '../CustomerPortal/AllCards';
import AllTokens from '../CustomerPortal/AllTokens';
import AllMandates from '../CustomerPortal/AllMandates';
import MandatePage from '../CustomerPortal/AllMandates/MandatePage';
import Login from './Pages/login';
import NotFoundPage from './Pages/404';
// import AUTH from "../auth";

function PrivateOutlet() {
  // Add your own authentication on the below line.
  // const isLoggedIn =  AUTH.isLoggedIn();
  const isLoggedIn = useSelector((state) => state.user.isLoggedIn);
  return isLoggedIn ? (
    <Layout>
      <Outlet />
    </Layout>
  ) : (
    <Navigate to="/login" />
  );
}

// eslint-disable-next-line react/prop-types
function LoginRoute({ children }) {
  // Add your own authentication on the below line.
  // const isLoggedIn =  AUTH.isLoggedIn();
  const isLoggedIn = useSelector((state) => state.user.isLoggedIn);
  return !isLoggedIn ? children : <Navigate to="/customer/cards" />;
}

const Routing = () => {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<PrivateOutlet />}>
          <Route path="" element={<h1>Home Page</h1>} />
          <Route path="tr">
            <Route path="" element={<TRStatus />} />
          </Route>
          <Route path="/customer/">
            <Route path="cards" element={<AllCards />} />
            <Route path="tokens/:card_data" element={<AllTokens />} />
            <Route path="mandates/:card_data" element={<AllMandates />} />
            <Route path="mandate/:card_data/:mandate_id" element={<MandatePage />} />
          </Route>
        </Route>
        <Route path="/onboarding" element={<OnBoarding />} />
        <Route
          path="/login"
          element={
            <LoginRoute>
              <Login />
            </LoginRoute>
          }
        />
        <Route path="/404" element={<NotFoundPage />} />
        <Route path="*" element={<NotFoundPage />} />
      </Routes>
    </Router>
  );
};

export default Routing;
