import centralApiInstance from './axiosInstances/central';

export function getOBOTRStatus() {
  const url = `/api/obotr/status`;
  return centralApiInstance.post(url);
}

export function getAllOBOTR() {
  const url = `/api/get_obotr`;
  return centralApiInstance.get(url);
}

export function registerOBOTR({ name, ceo_name, type }) {
  const url = `/api/register_obotr`;
  // eslint-disable-next-line object-property-newline
  return centralApiInstance.post(url, { name, ceo_name, type });
}
