import actionTypes from '../actionTypes/tokens';

export const setTokensRecords = (payload) => (dispatch) => {
  dispatch({
    payload,
    type: actionTypes.SET_TOKENS_RECORDS,
  });
};

export const updateToken = (payload) => (dispatch) => {
  dispatch({
    payload,
    type: actionTypes.UPDATE_TOKEN,
  });
};

export const deleteAllTokens = (payload) => (dispatch) => {
  dispatch({
    payload,
    type: actionTypes.DELETE_ALL_TOKENS,
  });
};
