import React, { useState } from "react";
import {
  InputGroup,
  FormControl,
  FormLabel,
  //   FormErrorMessage,
  //   FormHelperText,
  Input,
  Button,
  Container,
  Stack,
  Center,
  InputRightElement,
  Box,
  Alert,
  AlertIcon,
  Text,
  Flex,
  Image,
} from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
// import { Link } from 'react-router-dom';
import { login } from "../../actions/user";
import Footer from "../../components/Footer";
import Config from "../../config";

const Login = () => {
  const [show, setShow] = useState(false);
  const [credentials, setCredentials] = useState({});
  const dispatch = useDispatch();
  const togglePassword = () => setShow(!show);

  const onChangeHandler = ({ target }) => {
    setCredentials({ ...credentials, [target.name]: target.value });
  };

  const onLogin = () => {
    dispatch(login(credentials));
  };

  const userDetails = useSelector((state) => state.user);
  const loginStatusMessage = userDetails.status;

  return (
    <Container maxW="460px">
      <Box height="100vh" display="flex" alignItems="flex-end" flexWrap="wrap">
        <Box spacing={6} width="100%" boxShadow="md" rounded="md">
          <Flex
            padding={5}
            background="rgba(11, 112, 231, 0.05)"
            justifyContent="center"
          >
            <Box>
              <Image src={Config?.bankDetails?.logo} alt="Canara Bank" />
            </Box>
          </Flex>

          <Stack
            spacing={6}
            width="100%"
            boxShadow="md"
            rounded="md"
            padding="7"
          >
            <Box pb="3">
              <Text fontSize="24px" lineHeight="36px" color="#162F56" pb="1">
                Log in
              </Text>
              <Text fontSize="14px" lineHeight="20px" color="#162F568A">
                Enter your credentials to log in
              </Text>
            </Box>
            <FormControl id="email" isRequired>
              <FormLabel fontSize="12px" color="rgba(22, 47, 86, 0.54)">
                UserId
              </FormLabel>
              <Input
                type="text"
                placeholder="UserId"
                name="userId"
                onChange={onChangeHandler}
                value={credentials.userId || ""}
                variant="flushed"
                p={2}
                background="rgba(11, 112, 231, 0.05)"
                fontSize="14px"
              />
            </FormControl>
            <FormControl id="password" isRequired>
              <FormLabel fontSize="12px" color="rgba(22, 47, 86, 0.54)">
                Password
              </FormLabel>
              <InputGroup size="md">
                <Input
                  type={show ? "text" : "password"}
                  placeholder="Enter password"
                  name="password"
                  value={credentials.password || ""}
                  onChange={onChangeHandler}
                  variant="flushed"
                  p={2}
                  background="rgba(11, 112, 231, 0.05)"
                  fontSize="14px"
                />
                <InputRightElement width="4.5rem">
                  <Button
                    h="1.75rem"
                    size="sm"
                    onClick={togglePassword}
                    variant="link"
                    color="#03a9f4"
                  >
                    {show ? "Hide" : "Show"}
                  </Button>
                </InputRightElement>
              </InputGroup>
            </FormControl>
            <Center>
              <Button
                colorScheme="blue"
                size="md"
                height="48px"
                width="100%"
                border="2px"
                isLoading={false}
                onClick={onLogin}
              >
                Log In
              </Button>
            </Center>
            {/* <Box>
            <Center color="#63B3ED" as="u">
              <Link to="/onboarding">Not a member yet? Get on-boarded.</Link>
            </Center>
          </Box> */}
            {loginStatusMessage && (
              <Alert status="error">
                <AlertIcon />
                {loginStatusMessage}
              </Alert>
            )}
          </Stack>
        </Box>
        <Footer />
      </Box>
    </Container>
  );
};

export default Login;
