import produce from 'immer';
import actionTypes from '../actionTypes/mandates';

const initialState = {};

export default function editor(state = initialState, { payload, type }) {
  const nextState = produce(state, (draft) => {
    switch (type) {
      case actionTypes.SET_MANDATES_RECORDS: {
        return payload;
      }
      case actionTypes.UPDATE_MANDATE: {
        const { cardId, id, status } = payload;
        const updated = draft[cardId].map((e) => {
          if (e.id === id) {
            e.status = status;
            e.updated_at = +new Date();
          }

          return e;
        });

        draft[cardId] = updated;
        break;
      }
      case actionTypes.DELETE_MANDATE: {
        // eslint-disable-next-line object-property-newline
        const { cardId, id } = payload;
        const updated = draft[cardId].map((e) => {
          if (e.id === id) {
            e.status = 'deleted';
            e.updated_at = +new Date();
          }
          return e;
        });
        draft[cardId] = updated;
        break;
      }
      case actionTypes.DELETE_ALL_MANDATES: {
        // eslint-disable-next-line object-property-newline
        const { cardId } = payload;
        const updated = draft[cardId].map((e) => {
          e.status = 'deleted';
          e.updated_at = +new Date();

          return e;
        });
        draft[cardId] = updated;
        break;

        // return {};
      }
      default:
    }
  });

  return nextState;
}
