import actionTypes from '../actionTypes/user';
import config from '../config';
// import AUTH from "../auth";
import { loginUser } from '../apis/auth';

export const login = (payload) => async (dispatch) => {
  try {
    const { userId, password } = payload;
    // eslint-disable-next-line object-property-newline
    // const response = await loginUser({ id: userId, password });
    // const { data } = response;

    if (userId === 'test' && password === 'test!234') {
      dispatch({
        payload: {
          isLoggedIn: true,
          email: config.email,
          userId,
          name: config.name,
          refresh_token: 'abc',
          access_token: 'pqr',
        },
        type: actionTypes.LOGIN_USER,
      });
      // AUTH.setToken("pqr");
    } else {
      dispatch({
        type: actionTypes.FAILED_LOGIN,
        payload: { status: 'Invalid Credentials!' },
      });
    }

    // dispatch({
    //   payload: {
    //     isLoggedIn: true,
    //     email: config.email,
    //     userId,
    //     name: config.name,
    //     refresh_token: data.refresh_token,
    //     access_token: data.access_token,
    //   },
    //   type: actionTypes.LOGIN_USER,
    // });
  } catch (e) {
    dispatch({
      type: actionTypes.FAILED_LOGIN,
      payload: { status: e.message },
    });
  }
};

export const logout = (payload) => (dispatch) => {
  // AUTH.removeToken();
  dispatch({
    payload,
    type: actionTypes.LOGOUT_USER,
  });
};
