import Moment from 'moment';

export const titleCase = (str) => {
  if (!str) {
    // to handle empty string or null values
    str = '--';
  }
  const chars = str.split('');

  return chars[0].toUpperCase() + chars.splice(1).join('');
};

export const snakeToTitleCase = (str = '') => {
  return str
    .split('_')
    .map(titleCase)
    .join(' ');
};

export const formatCardNumber = (str) => {
  const res = 'XXXX XXXXX XXXX ' + str;
  return res;
};

export const timestampToDate = (timestamp) => {
  return new Moment(timestamp).format('hh:mm A MM/D/YY');
};
