import React from 'react';
import {
  Box,
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverBody,
  PopoverArrow,
  // PopoverCloseButton,
  PopoverHeader,
  PopoverFooter,
  Avatar,
  Text,
  Flex,
} from '@chakra-ui/react';
import { useSelector, useDispatch } from 'react-redux';
import { logout } from '../../actions/user';

const UserProfile = () => {
  const userDetails = useSelector((state) => state.user);
  const dispatch = useDispatch();

  const logoutHandler = () => {
    dispatch(logout());
  };

  return (
    <Popover>
      <PopoverTrigger>
        <Box width="180px">
          <Flex align="center" cursor="pointer">
            {/* <Avatar src="https://bit.ly/sage-adebayo" /> */}
            <Box ml="3">
              <Text fontWeight="bold">{userDetails.name}</Text>
            </Box>
          </Flex>
        </Box>
      </PopoverTrigger>
      <PopoverContent width="200px">
        <PopoverArrow />
        {/* <PopoverCloseButton my="2" /> */}
        <PopoverBody>
          {/* <Text py="1">Email: {userDetails.email}</Text>
          <Text py="1">UserId: {userDetails.userId}</Text>
          <Text fontWeight="bold" color="red" onClick={logoutHandler} cursor="pointer" py="1">
            Logout
          </Text> */}
          <PopoverHeader>
            <Text
              onClick={logoutHandler}
              cursor="pointer"
              py="1"
              colorScheme="#162F56"
              fontSize="14px"
            >
              Logout
            </Text>
          </PopoverHeader>
          <PopoverFooter>
            <Text
              cursor="pointer"
              py="1"
              colorScheme="#162F56"
              fontSize="14px"
              onClick={() => {
                window.open('https://canarabank.com/User_page.aspx?nid=14', '_blank');
              }}
            >
              Help and Support
            </Text>
          </PopoverFooter>
        </PopoverBody>
      </PopoverContent>
    </Popover>
  );
};

export default UserProfile;
