import React, { useState, useEffect, Component } from 'react';
import PropTypes from 'prop-types';
import { snakeToTitleCase } from '../../common/utils';

class Value extends Component {
  render() {
    return (
      <div className="table-data">
        {this.props.valueFn(this.props.item, this.props.hideTick, this.props.index)}
      </div>
    );
  }
}

const BorderedTable = ({ fields, items, onRowClick, selectedRowId, hideTick }) => {
  const [isEmptyData, setIsEmptyData] = useState(false);

  useEffect(() => {
    if (items?.length > 0) {
      setIsEmptyData(false);
    } else {
      setIsEmptyData(true);
    }
  }, [items]);

  return isEmptyData ? (
    <div className="table-row table-row-flexed">
      <div className="table-data no-data" colSpan="100%">
        No data to display
      </div>
    </div>
  ) : (
    items.map((item, idx) => (
      <div
        className={`table-row table-row-flexed bordered ${
          selectedRowId && selectedRowId === item.id ? 'active-token' : ''
        }`}
        key={idx}
        onClick={() => {
          onRowClick(item);
        }}
      >
        {fields.map((field, index) => (
          <Value key={index} index={index} item={item} valueFn={field[1]} hideTick={hideTick} />
        ))}
      </div>
    ))
  );
};

BorderedTable.propTypes = {
  fields: PropTypes.array,
  items: PropTypes.array,
  onRowClick: PropTypes.func,
  selectedRowId: PropTypes.any,
  hideTick: PropTypes.bool,
};

export default BorderedTable;
