import React, { useEffect, useState } from 'react';
import { Heading, Container, Center, Box } from '@chakra-ui/react';
import { getOBOTRStatus } from '../apis/tr';
// import { useSelector } from "react-redux";

const Status = () => {
  const [trDetails, setTRDetails] = useState({});

  useEffect(() => {
    (async function fetchDetails() {
      const response = await getOBOTRStatus();
      const { data } = response;
      setTRDetails(data.oboTrData);
    })();
  }, []);

  return (
    <Container maxW="container.md" padding="4" boxShadow="base">
      <Box>
        <Center>
          <Heading as="h2" size="lg" mb="5">
            Token Requestor Status
          </Heading>
        </Center>
      </Box>
      <Box mb="5">
        <Center>
          <Heading as="h4" size="md">
            Status: {trDetails.status || '-'}
          </Heading>
        </Center>
      </Box>
    </Container>
  );
};

export default Status;
