import axios from 'axios';
import envConfig from '../../config';
import { store } from '../../store';

// import AUTH from "../../auth";
import errorHandler from './errorhandler';

const centralApiInstance = axios.create({
  baseURL: envConfig.apiServerUrl,
});

centralApiInstance.interceptors.request.use((config) => {
  const { user } = store.getState();
  config.headers[envConfig.authHeaderKey] = `Bearer ${user.access_token}`;
  return config;
});

centralApiInstance.interceptors.response.use((response) => {
  return response;
}, errorHandler);

export default centralApiInstance;
