import React, { useState, useEffect } from "react";
import { useParams } from "react-router";
import { Link, useNavigate } from "react-router-dom";
import { AiOutlineArrowLeft, AiOutlineQuestionCircle } from "react-icons/ai";
import { BiError, BiCheck } from "react-icons/bi";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  useToast,
} from "@chakra-ui/react";
import { useSelector, useDispatch } from "react-redux";
import { setMandatesRecords, deleteAllMandates } from "../../actions/mandates";
import BorderedTable from "../../components/BorderedTable";
import { statusPill } from "../../common/ui/Pill";
import "../customer.css";
import { timestampToDate, snakeToTitleCase } from "../../common/utils";
import MandatesMockData from "../../mockData/mandates";
import CardsMockData from "../../mockData/cards";

const AllMandates = () => {
  const [selectedMandate, setSelectedMandate] = useState(null);
  const params = useParams();
  const navigate = useNavigate();
  const { card_data: cardId } = params;
  const dispatch = useDispatch();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const toast = useToast();

  const allMandateRecord = useSelector((state) => state.mandates);
  const Mandates = allMandateRecord[cardId] || [];

  const cardDetails = CardsMockData.filter(
    (e) => e.redacted_card_number === cardId
  )[0];

  useEffect(() => {
    if (selectedMandate?.id)
      navigate(`/customer/mandate/${cardId}/${selectedMandate.id}`);
  }, [selectedMandate]);

  useEffect(() => {
    if (Object.keys(allMandateRecord).length === 0) {
      dispatch(setMandatesRecords(MandatesMockData));
    }
  }, []);

  const handleMandate = () => {
    dispatch(deleteAllMandates({ cardId }));
    toast({
      title: "All Mandates Deleted Successfully",
      status: "success",
      isClosable: true,
      position: "top-right",
      duration: 3000,
    });
    onClose();
  };

  const fields = [
    [
      "Merchant",
      (item) => (
        <div className="table-data heimdall">
          {item.status !== "active" ? (
            <BiError className="red-error" />
          ) : (
            <BiCheck className="green-tick" />
          )}

          <div className="column">
            <span className="semi-bold">
              {snakeToTitleCase(item?.merchant)}
            </span>
            <span className="info-block">
              {item.status !== "active"
                ? `${item.status} at ${timestampToDate(item.updated_at)}`
                : `Created at ${timestampToDate(item.created_at)}`}
            </span>
          </div>
        </div>
      ),
    ],
    [
      "Status",
      (item) => <div className="table-data">{statusPill(item.status)}</div>,
    ],
    [
      "Frequency",
      (item) => (
        <div className="table-data semi-bold justify-center">
          {item.frequency}
        </div>
      ),
    ],
  ];

  return (
    <div>
      <div className="top-bar grey-strip">
        <div className="customer-left row-start">
          <Link to="/customer/cards">
            <AiOutlineArrowLeft />
          </Link>{" "}
          <span className="bold">
            XXXX XXXX XXXX {params?.card_data.substring(0, 4)}{" "}
            {statusPill("ACTIVE")}
          </span>
        </div>
        <div className="customer-left row-end">
          <button className="btn btn-hide-filters" onClick={onOpen}>
            Delete All Mandates
          </button>
        </div>
      </div>
      <div className="box">
        <div className="wrapper-prashant heimdall">
          <div className="">
            <div className="table-row detail-header table-row-flexed">
              <div className="table-data">Merchant</div>
              <div className="table-data">Status</div>
              <div className="table-data justify-center">Frequency</div>
            </div>
            <div className="table-bordered">
              <BorderedTable
                items={Mandates}
                fields={fields}
                selectedRowId={selectedMandate?.token_id}
                onRowClick={setSelectedMandate}
              />
            </div>
          </div>

          <div className="">
            <div className="boogey">
              <div className="detail-header">Card Details</div>
              <div className="token-info">
                <div className="table-row">
                  <div className="table-data">Card</div>
                  <div className="table-data">
                    {cardDetails.network} {params?.card_data}
                  </div>
                </div>
                <div className="table-row">
                  <div className="table-data">Active Mandates</div>
                  <div className="table-data">
                    {Mandates?.filter((obj) => obj.status === "active").length}
                  </div>
                </div>
                <div className="table-row">
                  <div className="table-data">Paused Mandates</div>
                  <div className="table-data">
                    {Mandates?.filter((obj) => obj.status === "paused").length}
                  </div>
                </div>
                <div className="table-row">
                  <div className="table-data">Deleted Mandates</div>
                  <div className="table-data">
                    {Mandates?.filter((obj) => obj.status === "deleted").length}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal isOpen={isOpen} onClose={onClose} size="sm">
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>Delete All mandate?</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            This action cannot be undone. Are you sure you want to delete all
            mandates?
          </ModalBody>

          <ModalFooter flexDirection="row" justifyContent="space-between">
            <button
              className="customer-btn btn-hide-filters equal-width"
              onClick={onClose}
            >
              No
            </button>
            <button
              className="customer-btn btn-hide-filters equal-width fill"
              onClick={handleMandate}
            >
              Yes
            </button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </div>
  );
};

export default AllMandates;
