import produce from 'immer';
import actionTypes from '../actionTypes/user';

const initialState = {
  isLoggedIn: false,
  status: '',
  refresh_token: '',
  access_token: '',
  userId: '',
  name: '',
  email: '',
};

export default function editor(state = initialState, { payload, type }) {
  const nextState = produce(state, () => {
    switch (type) {
      case actionTypes.LOGIN_USER: {
        // eslint-disable-next-line object-property-newline
        return { ...payload, status: '' };
      }
      case actionTypes.LOGOUT_USER: {
        return initialState;
      }
      case actionTypes.FAILED_LOGIN: {
        // eslint-disable-next-line object-property-newline
        return { ...state, status: payload.status };
      }
      default:
    }
  });

  return nextState;
}
