import actionTypes from '../actionTypes/mandates';

export const setMandatesRecords = (payload) => (dispatch) => {
  dispatch({
    payload,
    type: actionTypes.SET_MANDATES_RECORDS,
  });
};

export const updateMandate = (payload) => (dispatch) => {
  dispatch({
    payload,
    type: actionTypes.UPDATE_MANDATE,
  });
};

export const deleteMandate = (payload) => (dispatch) => {
  dispatch({
    payload,
    type: actionTypes.DELETE_MANDATE,
  });
};

export const deleteAllMandates = (payload) => (dispatch) => {
  dispatch({
    payload,
    type: actionTypes.DELETE_ALL_MANDATES,
  });
};
