import React, { useState } from 'react';
import {
  FormControl,
  FormLabel,
  // FormErrorMessage,
  // FormHelperText,
  Input,
  Select,
  Button,
  Container,
  Stack,
  Center,
  Heading,
  Box,
  Text,
  Alert,
  AlertIcon,
} from '@chakra-ui/react';
// import FileUpload from "../common/FileUpload";
// import { useSelector } from "react-redux";
import { Link } from 'react-router-dom';
import { registerOBOTR } from '../apis/tr';

const OnBoardingForm = () => {
  // const onFileChange = (e) => {
  //   console.log(e.target.value);
  // };
  // const userDetails = useSelector((state) => state.user);

  const [onBoarded, setOnBoarded] = useState(false);
  const [trDetails, setTrDetails] = useState({});
  const [formData, setFormData] = useState({});

  const onFormDataChange = ({ target }) => {
    const { name, value } = target;
    setFormData({ ...formData, [name]: value });
  };

  const onSubmit = async () => {
    try {
      const response = await registerOBOTR({
        name: formData['company-name'],
        ceo_name: formData['ceo-name'],
        type: formData['requestor-type'],
      });

      const { data } = await response;
      setTrDetails(data);
      setOnBoarded(true);
      setFormData({});
    } catch (e) {
      console.log(e);
    }
  };

  const AfterOnboardingUI = (
    <Stack spacing={7}>
      <Center>
        <Box>
          <Text fontSize="22px">OboTR-Id:- {trDetails.oboTrID}</Text>
          <Text fontSize="22px">loginId:- {trDetails.loginID}</Text>
          <Text fontSize="22px">Password:- {trDetails.password}</Text>
          <Text fontSize="22px" color="tomato">
            Status:- {trDetails.status}
          </Text>
        </Box>
      </Center>
      <Alert status="success">
        <AlertIcon />
        You're registered, use this loginID and Password to login.&nbsp;&nbsp;
        <Text fontSize="17px" color="#ED64A6">
          <Link to="/login">Go to the login page.</Link>
        </Text>
      </Alert>
      {/* <Box>
        <Center color="#63B3ED" as="u">
          <Link to="/login">login.</Link>
        </Center>
      </Box> */}
    </Stack>
  );

  // eslint-disable-next-line no-shadow
  const OnBoardingForm = (
    <Stack spacing={7}>
      <FormControl id="company-name">
        <FormLabel>Company Name</FormLabel>
        <Input
          type="text"
          placeholder="Enter company name..."
          name="company-name"
          value={formData['company-name'] || ''}
          onChange={onFormDataChange}
        />
      </FormControl>
      <FormControl id="ceo-name">
        <FormLabel>Ceo Name</FormLabel>
        <Input
          type="text"
          placeholder="Enter ceo name..."
          name="ceo-name"
          value={formData['ceo-name'] || ''}
          onChange={onFormDataChange}
        />
      </FormControl>
      {/* <FormControl id="registered-address">
      <FormLabel>Registered Address</FormLabel>
      <Input
        type="text"
        placeholder="Enter registered address..."
        name="registered-address"
      />
    </FormControl>
    <FormControl id="signed-aggreement">
      <FormLabel>Signed Aggreement</FormLabel>
      <FileUpload
        accept="application/pdf"
        name="signed-aggreement"
        onChange={onFileChange}
      >
        <Button>Upload Signed Aggreement</Button>
      </FileUpload>
    </FormControl>
    <FormControl id="aadhar">
      <FormLabel>Aadhar</FormLabel>
      <FileUpload name="signed-aggreement" onChange={onFileChange}>
        <Button>Upload Aadhar</Button>
      </FileUpload>
    </FormControl> */}
      <FormControl id="requestor-type">
        <FormLabel>Token Requestor Type</FormLabel>
        <Select
          placeholder="Select Token Requestor"
          name="requestor-type"
          value={formData['requestor-type'] || ''}
          onChange={onFormDataChange}
        >
          <option value="pa">PA</option>
          <option value="merchant">Merchant</option>
        </Select>
      </FormControl>
      <Center>
        <Button
          colorScheme="blue"
          size="md"
          height="48px"
          width="200px"
          border="2px"
          onClick={onSubmit}
        >
          Submit
        </Button>
      </Center>
      <Box>
        <Center color="#63B3ED" as="u">
          <Link to="/login">Already registered? login.</Link>
        </Center>
      </Box>
    </Stack>
  );

  const renderUI = onBoarded ? AfterOnboardingUI : OnBoardingForm;

  return (
    <Container maxW="container.md" padding="4" boxShadow="base" mt="10">
      <Center mb="6">
        <Heading as="h2" size="lg">
          Token Requestor Onboarding
        </Heading>
      </Center>
      {renderUI}
    </Container>
  );
};

export default OnBoardingForm;
